"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";

export default function LogoutPage() {
  const router = useRouter();

  useEffect(() => {
    // حذف التوكن من الكوكيز مش من localStorage (لأنك تستخدم cookies مش localStorage)
    document.cookie = "token=; Max-Age=0; path=/;"; // حذف التوكن

    console.log("User logged out");

    // إعادة توجيه المستخدم لصفحة الدخول
    router.replace("/auth");
  }, [router]);

  return null; // أو Spinner صغير لو تحب
}
